* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  background-color: hsl(var(--document-root-background));
  color: hsl(var(--document-root-color));
  font-family: var(--document-root-font-family);
  font-size: var(--document-root-font-size);
  font-weight: var(--document-root-font-weight);
  line-height: var(--document-root-line-height);
}

*::selection {
  background-color: hsl(var(--document-root-color));
  color: hsl(var(--document-root-background));
}

ol,
ul {
  list-style-position: inside;
}

ol,
ul {
  display: block;
  margin: var(--lists-margin);
  padding: 0;
}

dt {
  font-size: var(--lists-font-size-heading);
  font-weight: var(--lists-font-weight-heading);
}

dl {
  display: block;
  margin: var(--lists-definition-margin);
  padding: 0;
}

dd {
  margin: var(--lists-definition-margin-item);
}

li {
  margin: var(--lists-margin-item);
}

dd:first-child,
li:first-child {
  margin-top: 0;
}
dd:last-child,
li:last-child {
  margin-bottom: 0;
}

