input[type=checkbox].sheet {
  display: none;
}
input[type=checkbox].sheet:not(:checked) + [role=dialog] {
  display: none;
}
input[type=checkbox].sheet + [role=dialog] {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  scroll-snap-type: y mandatory;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: var(--sheet-container-z-index);
}
input[type=checkbox].sheet + [role=dialog][data-palette=accent] {
  --background: var(--sheet-accent-background);
  --color: var(--sheet-accent-color);
}
input[type=checkbox].sheet + [role=dialog][data-palette=dark] {
  --background: var(--sheet-dark-background);
  --color: var(--sheet-dark-color);
}
input[type=checkbox].sheet + [role=dialog][data-palette=light] {
  --background: var(--sheet-light-background);
  --color: var(--sheet-light-color);
}
input[type=checkbox].sheet + [role=dialog][data-palette=alert] {
  --background: var(--sheet-alert-background);
  --color: var(--sheet-alert-color);
}
input[type=checkbox].sheet + [role=dialog][data-palette=affirmative] {
  --background: var(--sheet-affirmative-background);
  --color: var(--sheet-affirmative-color);
}
input[type=checkbox].sheet + [role=dialog][data-palette=negative] {
  --background: var(--sheet-negative-background);
  --color: var(--sheet-negative-color);
}
input[type=checkbox].sheet + [role=dialog]::before {
  display: block;
  content: "";
  scroll-snap-align: start;
}
input[type=checkbox].sheet + [role=dialog] > label {
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsl(var(--sheet-backdrop-background));
  z-index: var(--sheet-backdrop-z-index);
}
input[type=checkbox].sheet + [role=dialog] > [role=document] {
  max-height: 100vh;
  margin-top: var(--sheet-content-margin);
  scroll-snap-align: end;
}
input[type=checkbox].sheet + [role=dialog][data-palette] > [role=document] {
  background: hsl(var(--background));
  color: hsl(var(--color));
  --inherited-background: var(--color);
  --inherited-color: var(--background);
  padding: var(--sheet-content-padding);
  border-radius: var(--sheet-content-radius);
}
input[type=checkbox].sheet + [role=dialog][data-palette] > [role=document]::selection, input[type=checkbox].sheet + [role=dialog][data-palette] > [role=document] *::selection {
  background-color: hsl(var(--color));
