    --spacing: var(--stack-spacing-medium) !important;
  }
}
@media (--viewports-large) {
  .stack[data-spacing~="large+large"] {
    --spacing: var(--stack-spacing-large) !important;
  }
}
@media (--viewports-large) {
  .stack[data-spacing~="huge+large"] {
    --spacing: var(--stack-spacing-huge) !important;
  }
}
.stack[data-spacing~=tiny] {
  --spacing: var(--stack-spacing-tiny);
}
.stack[data-spacing~=small] {
  --spacing: var(--stack-spacing-small);
}
.stack[data-spacing~=medium] {
  --spacing: var(--stack-spacing-medium);
}
.stack[data-spacing~=large] {
  --spacing: var(--stack-spacing-large);
}
.stack[data-spacing~=huge] {
  --spacing: var(--stack-spacing-huge);
}
.stack:not([data-orientation]) {
  --margin: var(--spacing) 0px 0px 0px;
  flex-direction: column;
  align-items: var(--alignment-x);
  justify-content: var(--alignment-y);
}
.stack[data-orientation=horizontal] {
  --margin: 0px 0px 0px var(--spacing);
  flex-direction: row;
  align-items: var(--alignment-y);
  justify-content: var(--alignment-x);
}
.stack > * + * {
  margin: var(--margin) !important;
}
.stack > :first-child {
  margin: 0 !important;
}

/**
 * HACK: Ideally we would just want to check for a blank attribute, e.g. `<div data-hidden>`, however
 * CSS doesn't support a selector that can accomplish that
 */
[data-hidden]:not([data-hidden~=tiny], [data-hidden~=small], [data-hidden~=medium], [data-hidden~=large]) {
  display: none !important;
}

@media (--viewports-tiny) {
  [data-hidden~=tiny] {
    display: none !important;
  }
}
@media (--viewports-small) {
  [data-hidden~=small] {
    display: none !important;
  }
}
@media (--viewports-medium) {
  [data-hidden~=medium] {
    display: none !important;
  }
}
@media (--viewports-large) {
  [data-hidden~=large] {
    display: none !important;
  }
}